

















































































































































































@import "@/styles/util/util.scss";

.about-text {
    @include rem((padding-top: 60px));
    @include rem((padding-bottom: 60px));
    h2 {
        @include rem((margin-bottom: 20px));
        font-weight: bold;
    }
    .who-is-behind {
        @include rem((margin-bottom: 60px));
    }
    .company-philosophy,
    .double-power {
        @include rem((margin: 60px auto 60px auto));
        h2 {
            color: $color-red-t2w;
            text-align: right;
        }
        p {
            text-align: right;
        }
        button {
            float: right;
        }
        button.more {
            background: $color-black-medium;
            color: $color-white;
            border-color: $color-black;
            float: right;
        }
    }
    .solution-practicable {
        @include rem((margin-top: 60px));
    }
    .fade-enter-active,
    .fade-leave-active {
        transition: opacity 0.5s;
    }
    .fade-enter,
    .fade-leave-to {
        opacity: 0;
    }
}
.about-photos {
    @include rem((padding: 60px 0 60px 0));
    h2.title {
        @include rem((margin-bottom: 60px));
        font-weight: bold;
        text-align: center;
    }
    img.photo {
        @include transition(all 0.3s ease);
        display: block;
        margin: 0 auto;
        width: 150px;
        -webkit-filter: grayscale(0%);
        filter: grayscale(0%);
        &:hover {
            @include transition(all 0.3s ease);
            -webkit-filter: grayscale(10%);
            filter: grayscale(10%);
        }
    }
    h4 {
        @include rem((margin: 30px 0 30px 0));
        text-align: center;
        font-weight: bold;
        span {
            @include rem(font-size, 15px);
        }
    }
    p {
        @include rem(font-size, 15px);
        text-align: center;
        margin: 0;
        a {
            @include rem(font-size, 15px);
            color: $color-black-medium;
            text-decoration: none;
            &:hover {
                color: $color-black-medium;
                cursor: pointer;
                text-decoration: none;
            }
        }
    }
    ul.social {
        @include rem((margin-top: 10px));
        @include rem((padding: 0));
        text-align: center;
        li {
            @include rem((margin-right: 10px));
            display: inline;
            a {
                @include transition(all 0.3s ease);
                text-decoration: none;
                color: $color-black-medium;
                &:hover {
                    @include transition(all 0.3s ease);
                    color: $color-red-t2w;
                }
            }
        }
    }
}
